import React from "react"
import Layout from "../../components/layout"
import SEO from "../../components/seo"
import axios from "axios"
import Highcharts from 'highcharts/highstock'
import moment from 'moment'
import HighchartsReact from 'highcharts-react-official'

import "tabler-react/dist/Tabler.css";

import { Card, Page } from "tabler-react";

class FLUExpress extends React.Component {
  
  fetchFLUExpress(){    
    var _this = this;
    
      axios
        .get(`https://ourhealthcarehk.github.io/api/FLUEXPRESS.json`)
        .then(({ data }) => {
          
          
          console.log(data);
          
          if (!data)
            return;
          
	        var flu_sample_count_series = [];
	        var flu_outbreaks = [];
	
          
          for (var i = 6; i < 10; i++) {
          	var seriesNum = i-6;
    	      var dataLabel = ["A(H1)甲型(H1)", "A(H3)甲型(H3)", "B乙型", "C丙型"]
          	flu_sample_count_series[seriesNum] = {name:dataLabel[seriesNum],data:[]};
    	for (var j=3; j<data.length; j++){
    	  if (data[j][3].length !== 0)
    		flu_sample_count_series[seriesNum].data.push(
    			[moment(data[j][3], 'DD/MM/YYYY').valueOf(),
    			parseInt(data[j][i])]);
    	}
          }
          for (i = 16; i < 17; i++) {
    	flu_outbreaks[0] = {name:"學校/院舍爆發宗數", data: []};
    	for (j=3; j<data.length; j++){
    	  if (data[j][3].length !== 0)
    		flu_outbreaks[0].data.push(
    			[moment(data[j][3], 'DD/MM/YYYY').valueOf(),
    			parseInt(data[j][i])]);
    	}	
          }
          _this.state.labsurveillance.series = flu_sample_count_series;
          _this.state.outbreaks.series = flu_outbreaks;
          
          _this.setState(_this.state);
                    
        })
        .catch(error => {
          _this.setState({ loading: false, error })
        })
  }
  
  constructor() {
    super()

    this.state = {
      labsurveillance: {},
      outbreaks: {},      
    };
    this.state.labsurveillance.series = Object.assign({ series: {}}, this.settings);
    this.state.outbreaks.series = Object.assign({ series: {}}, this.settings);
    this.settings = {
      rangeSelector: {
    	    buttons: [
        {
    		    type: 'month',
    		    count: 3,
    		    text: '三個月'
    		}, {
    		    type: 'month',
    		    count: 6,
    		    text: '六個月'
    		}, {
    		    type: 'year',
    		    count: 1,
    		    text: '一年'
    		}, {
    		    type: 'all',
    		    text: '全部'
    		}],
            },
	
        	time: {
            	useUTC: false
    	},
            yAxis: {
                plotLines: [{
                    value: 0,
                    width: 2,
                    color: 'silver'
                }]
            },
            tooltip: {
                pointFormat: '<span style="color:{series.color}">{series.name}</span>: <b>{point.y}</b>)<br/>',
                split: true
            }};
    
  }
  
	componentDidMount() {
    this.fetchFLUExpress();
	}
  
	render() {
    
      return (
      <Layout>
    <SEO title="本地流感流行情況" description="衛生署每週更新的流感監測數據，用以衡量流感流行程度及對公共醫療系統的影響。"/>
    <Page.Content title="本地流感流行情況">
      <Card
        title="實驗室監測">
          <Card.Body>        <p>公共衞生化驗服務處所收集的呼吸道樣本中，季節性流感病毒陽性百分比。當中分別顯示季節性流感病毒的甲型(H1)、甲型(H3)、乙型及丙型流感株類。</p>
        <HighchartsReact style={{ width: "100%", minHeight: "100px"}}
        highcharts={Highcharts}
        constructorType={'stockChart'}
        options={this.state.labsurveillance}
      />
        </Card.Body>
      </Card>
      <Card
        title="流感樣疾病爆發監測">
        <Card.Body>
        <HighchartsReact style={{ width: "100%", minHeight: "100px" }}
        highcharts={Highcharts}
        constructorType={'stockChart'}
        options={this.state.outbreaks}
      />
        </Card.Body>
      </Card>
    </Page.Content>
      </Layout>
      );
  }
}

export default FLUExpress
